import {WanderGame} from "./wander-game";
import {QuestTask} from "./quests/quest-task";

export abstract class WanderScene {

	static LEAVESTATE_UNFINISHED = -1;
	static LEAVESTATE_FINISHED = 0;

	game: WanderGame;
	// a scene that CONTAINS the current scene, if applicable, so the Storyteller will know what scene to pass to when this ends.
	narrative: WanderScene;

	// TODO: add interface for specific scene UI (not the SceneUI master wrapper) so the Scene can interact with its own display
	leaf: any;

	// TODO: add QuestTask class as driver for the scene
	driver: QuestTask;

	protected constructor (game: WanderGame)
	{
		this.game = game;

		// narrative should be set outside the constructor
		this.narrative = null;

		// TODO: TBA (note: should get value AFTER constructor anyway?)
		this.leaf = null;
		this.driver = null;
	}

	setNarrative (narrative: WanderScene)
	{
		this.narrative = narrative;
	}

	abstract sceneFinished(): boolean;

	/**
	 * Acts as default, subclasses can override if they have specific return codes > 0 (for success and < -1 for failure?)
	 * If the scene is unfinished, subclasses should still respect the UNFINISHED constant
	 */
	returnState(): number
	{
		// Scenes that do not have a set of return values always return 0 on end OR -1 if not finished
		return this.sceneFinished() ? WanderScene.LEAVESTATE_FINISHED : WanderScene.LEAVESTATE_UNFINISHED;
	}

	/**
	 * Acts as default, subclasses can override if they chain to a scene they create
	 */
	getFollowingScene (): WanderScene
	{
		return this.narrative;
	}

}

/**
 * Wrapper for Map type for syntactical cleanup
 */
class LeaveStatesMap extends Map<string, number> {}

export class WanderSceneTrigger {

	/*
		// if all conditions in this list are met, the trigger is triggered
		private ArrayList<SceneTriggerCondition> needed;
		// if any condition in this list is met, the trigger is not triggered
		private ArrayList<SceneTriggerCondition> blockers;
	*/

	needed: WanderSceneTriggerCondition[];
	blockers: WanderSceneTriggerCondition[];

	constructor ()
	{
		this.needed = [];
		this.blockers = [];
	}

	/**
	 * Adds a condition, by default as needed unless specified as blocker
	 * @param condition trigger condition for this scene
	 * @param asblocker whether the condition is a blocker, defaults to needed
	 */
	addCondition (condition: WanderSceneTriggerCondition, asblocker: boolean = false)
	{
		(asblocker ? this.blockers : this.needed).push(condition);
	}

	isActive (leavestates: LeaveStatesMap): boolean
	{
		for (let triggercondition of this.blockers)
		{
			if (triggercondition.checkCondition(leavestates))
			{
				return false;
			}
		}

		for (let triggercondition of this.needed)
		{
			if (!triggercondition.checkCondition(leavestates))
			{
				return false;
			}
		}
		return true;
	}

	static createFromJSON (src: object): WanderSceneTrigger
	{
		const trigger = new WanderSceneTrigger();
		if (src.hasOwnProperty("c_for"))
		{
			for (let conditiondef of src["c_for"])
			{
				trigger.addCondition(WanderSceneTriggerCondition.createFromJSON(conditiondef), false);
			}
		}
		if (src.hasOwnProperty("c_against"))
		{
			for (let conditiondef of src["c_against"])
			{
				trigger.addCondition(WanderSceneTriggerCondition.createFromJSON(conditiondef), true);
			}
		}
		return trigger;
	}

}

export class WanderSceneTriggerCondition {

	sceneid: string;
	statevalue: number;
	mustmeet: boolean;


	constructor(sceneid: string, valuestate: number, mustmeet: boolean)
	{
		this.sceneid = sceneid;
		this.statevalue = valuestate;
		this.mustmeet = mustmeet;
	}

	checkCondition (leavestates: LeaveStatesMap): boolean
	{
		let leavestate = leavestates.has(this.sceneid) ? leavestates.get(this.sceneid) : WanderScene.LEAVESTATE_UNFINISHED;
		return this.mustmeet ? leavestate === this.statevalue : leavestate !== this.statevalue;
	}

	static createFromJSON (src: object): WanderSceneTriggerCondition
	{
		const sceneid = src["sceneid"];
		const statevalue = src["endstate"];
		let mustmeet = true;
		if (src.hasOwnProperty("mustnot")) {
			mustmeet = !src["mustnot"];
		}
		return new WanderSceneTriggerCondition(sceneid, statevalue, mustmeet);
	}

}
